import React, { Component } from "react";
import { Avatar, Drawer, Divider } from "antd";
import {
	MobileOutlined,
	MailOutlined,
	UserOutlined,
	CompassOutlined,
	CalendarOutlined,
	FacebookOutlined,
	InstagramOutlined,
	TwitterOutlined,
	GlobalOutlined,
} from "@ant-design/icons";

export class UserView extends Component {
	render() {
		const { data, visible, close } = this.props;
		const country = ["United States", "Netherland", "Ukraine"];
		return (
			<Drawer
				width={300}
				placement="right"
				onClose={close}
				closable={false}
				open={visible}
			>
				<div className="text-center mt-3">
					<Avatar size={80} src={data?.avatar} />
					<h3 className="mt-2 mb-0">
						{data?.firstname + " " + data?.lastname}
					</h3>
					<span className="text-muted">{data?.title}</span>
				</div>
				<Divider dashed />
				<div className="">
					<h6 className="text-muted text-uppercase mb-3">Account details</h6>
					<p>
						<UserOutlined />
						<span className="ml-3 text-dark">id: {data?.userID}</span>
					</p>
					<p>
						<CalendarOutlined />
						<span className="ml-3 text-dark">Born in {data?.birthday}</span>
					</p>
				</div>
				<div className="mt-5">
					<h6 className="text-muted text-uppercase mb-3">CONTACT</h6>
					<p>
						<MobileOutlined />
						<span className="ml-3 text-dark">
							{data?.phoneNumber ? data?.phoneNumber : "___.____.__.____"}
						</span>
					</p>
					<p>
						<MailOutlined />
						<span className="ml-3 text-dark">
							{data?.email ? data?.email : "-"}
						</span>
					</p>
					<p>
						<CompassOutlined />
						<span className="ml-3 text-dark">{country[data?.country - 1]}</span>
					</p>
				</div>
				<div className="mt-5">
					<h6 className="text-muted text-uppercase mb-3">Social profiles</h6>
					<p>
						<FacebookOutlined />
						<a href="/#" className="ml-3 text-dark">
							{data?.facebook ? data?.facebook : "-"}
						</a>
					</p>
					<p>
						<TwitterOutlined />
						<a href="/#" className="ml-3 text-dark">
							{data?.twitter ? data?.twitter : "-"}
						</a>
					</p>
					<p>
						<InstagramOutlined />
						<a href="/#" className="ml-3 text-dark">
							{data?.instagram ? data?.instagram : "-"}
						</a>
					</p>
					<p>
						<GlobalOutlined />
						<a href="/#" className="ml-3 text-dark">
							{data?.site ? data?.site : "-"}
						</a>
					</p>
				</div>
			</Drawer>
		);
	}
}

export default UserView;
